/*#region Grids*/
.Album {
  display: grid;
  grid-template-areas: "Title" "Photo" "Download" "Buttons";
}

.Title {
  grid-area: Title;
}

.Photo {
  grid-area: Photo;
  width: 100%;
}

.Download {
  grid-area: Download;
}

.Buttons {
  grid-area: Buttons;
  display: grid;

  grid-template-areas: "PrevsButton NextButton";
}

.PrevButton {
  grid-area: PrevsButton;
}

.NextButton {
  grid-area: NextButton;
}
/*#endregion*/
